import { put, takeLatest } from 'redux-saga/effects';


const fetchPostAPI = async ({payload}) => {
  const postId = payload.postId;
  
  const result = await fetch(`https://5e6f7fe1f5e95b0016d4752c.mockapi.io/api/v1/posts/${postId}`);
  return await result.json();
}

function* fetchPost(payload) {
  try {
    const response = yield fetchPostAPI(payload);
    yield put({ type: "POST_FETCH_SUCCEEDED", response });
  } catch(err) {
    yield put({ type: 'POST_FETCH_FAILED', err });
  }
}

 
export default function* postSaga() {
  yield takeLatest('POST_FETCH_REQUESTED', fetchPost)
}


 