import { put, takeLatest } from 'redux-saga/effects';

const fetchPostsAPI = async () => {
  const result = await fetch(`https://5e6f7fe1f5e95b0016d4752c.mockapi.io/api/v1/posts`);
  return await result.json();
}

function* fetchPosts() {
  try {
    const response = yield fetchPostsAPI();
    yield put({ type: "POSTS_FETCH_SUCCEEDED", response });
  } catch(err) {
    yield put({ type: 'POSTS_FETCH_FAILED', err });
  }
}

 
export default function* postsSaga() {
  yield takeLatest('POSTS_FETCH_REQUESTED', fetchPosts)
}