const initialState = {
    loading: false,
    post: null,
    error: null
};
  
export default (state = initialState, action) => {
    switch (action.type) {
        case 'POST_FETCH_REQUESTED':
            return Object.assign({}, state)
        case 'POST_FETCH_SUCCEEDED':
            return Object.assign({}, state, { post: action.response.data.post, loading: true })
        case 'POST_FETCH_FAILED':
            return Object.assign({}, state, { error: action.response.data.message })
        default:
            return state;
    }
};

 