const initialState = {
    loading: false,
    posts: [],
    error: null
};
  
export default (state = initialState, action) => {
    switch (action.type) {
        case 'POSTS_FETCH_REQUESTED':
            return { state };
        case 'POSTS_FETCH_SUCCEEDED':
            return { state, posts: action.response.data.posts }
        case 'POSTS_FETCH_FAILED':
            return { state, error: action.response.data.message }
        default:
            return state;
    }
};