const initialState = {
    loading: false,
    users: [],
    error: null
};
  
export default (state = initialState, action) => {
    switch (action.type) {
        case 'USER_FETCH_REQUESTED':
            return { state };
        case 'USER_FETCH_SUCCEEDED':
            return { state, users: action.response.data.posts }
        case 'USER_FETCH_FAILED':
            return { state, error: action.response.data.message }
        default:
            return state;
    }
};